import { Component, OnInit } from '@angular/core';
import {AngularFireDatabase} from '@angular/fire/database';

@Component({
  selector: 'app-goals',
  templateUrl: './goals.component.html',
  styleUrls: ['./goals.component.scss']
})
export class GoalsComponent implements OnInit {
  weekdays = [
    {dayNumber: 1, dayName: "Monday", selected: true},
    {dayNumber: 2, dayName: "Tuesday", selected: false},
    {dayNumber: 3, dayName: "Wednesday", selected: false},
    {dayNumber: 4, dayName: "Thursday", selected: false},
    {dayNumber: 5, dayName: "Friday", selected: false},
    {dayNumber: 6, dayName: "Saturday", selected: false},
    {dayNumber: 7, dayName: "Sunday", selected: false}
];

mondayGoals = [];
tuesdayGoals = [];
wednesdayGoals = [];
thursdayGoals = [];
fridayGoals = [];
saturdayGoals = [];
sundayGoals = [];

goalsArray : any[];

goalName = "Goal Name";
  constructor(private db: AngularFireDatabase) {
    
    db.list("/goals", ref=> ref.orderByChild("playerId").equalTo(1)).valueChanges()
    .subscribe((result)=> {
      this.goalsArray = result;
      console.log(this.goalsArray);
      this.mondayGoals = this.goalsArray.filter(x=> x.weekdays.some((element)=> element.dayNumber == 1));
      this.tuesdayGoals = this.goalsArray.filter(x=> x.weekdays.some((element)=> element.dayNumber == 2));
      this.wednesdayGoals = this.goalsArray.filter(x=> x.weekdays.some((element)=> element.dayNumber == 3));
      this.thursdayGoals = this.goalsArray.filter(x=> x.weekdays.some((element)=> element.dayNumber == 4));
      this.fridayGoals = this.goalsArray.filter(x=> x.weekdays.some((element)=> element.dayNumber == 5));
      this.saturdayGoals = this.goalsArray.filter(x=> x.weekdays.some((element)=> element.dayNumber == 6));
      this.sundayGoals = this.goalsArray.filter(x=> x.weekdays.some((element)=> element.dayNumber == 7));
    });

   }

  ngOnInit() {
  }

  selectDay(dayOfWeek){
    this.weekdays.forEach((item)=>{
      if(item.dayNumber == dayOfWeek.dayNumber){
        item.selected = !item.selected;
      }
    });
  }

  

  addGoal(goalName){
    console.log("clicked");
    var dbGoalModel = {
      name: goalName,
      playerId: 1,
      weekdays: this.weekdays.filter(x=> x.selected)
    };
    this.db.list("goals").push(dbGoalModel);
  }

  completeGoal(goal){
    goal.completed = !goal.completed;
    this.db.list('goals').update(goal.$key, goal);
  }




  

}
